import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/HomePage.vue'
import Login from '../views/LoginPage.vue'
import breakup from '../views/BreakupPage.vue'
import dateA from '../views/230504Page.vue'
import dateB from '../views/230508Page.vue'
import dateC from '../views/230518Page.vue'

const routes = [
  {
    path: '/',
    component: Home
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/breakup',
    component: breakup
    // beforeEnter: function(to, from, next) {
    //   if (isLogin) {
    //     next()
    //   }
    // }
  },
  {
    path: '/dateA',
    component: dateA
  },
  {
    path: '/dateB',
    component: dateB
  },
  {
    path: '/dateC',
    component: dateC
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior () { return { top: 0 } }
})

export default router
